}

.c-spinner {
  width: 100%;
}

.c-spinner input {
  float: left;
  width: 40px;
  padding: 6px;
  border-radius: 0;
  border-color: #d0d7de;
}

.c-input-group-btn-vertical {
  position: relative;
  white-space: nowrap;
  width: 1%;
  vertical-align: middle;
  display: inline-block;
}

.c-input-group-btn-vertical i {
  position: absolute;
  top: 0;
  left: 4px;
}

.c-input-group-btn-vertical .btn {
  display: block;
  float: none;
  max-width: 100%;
  padding: 8px;
  margin-left: -1px;
  position: relative;
  border-radius: 0;
  color: #d0d7de;
  border-color: #d0d7de;
}

.c-input-group-btn-vertical .btn:first-child {
  border-top-right-radius: 0;
}

.c-input-group-btn-vertical .btn:last-child {
  margin-top: -2px;
  border-bottom-right-radius: 0;
}

.simplegallery {
  margin: 0 auto;
  width: 417px;
  height: 380px;
  overflow: hidden;
}

.simplegallery .content {
  background: #fff;
  position: relative;
  width: 418px;
  height: 270px;
  overflow: hidden;
}

.simplegallery .content img {
  width: 415px;
  border: 1px solid #9c9c9c;
}

.simplegallery .thumbnail {
  margin-top: 5px;
}

.simplegallery .thumbnail .thumb {
  float: left;
  width: 100px;
  height: 100px;
  margin: 0 5px 0 0;
  cursor: pointer;
}

.simplegallery .thumbnail .thumb img {
  border: 1px solid #9c9c9c;
  width: 100px;
}

.simplegallery .thumbnail .last {
  margin: 0;
}

